import { combineReducers } from 'redux';
import { andOrToggleReducer } from '../components/AndOrToggler/AndOrTogglerSlice';
import { inputFilterReducer } from '../components/InputFilter/InputFilterSlice';
import { optionsFilterReducer } from '../components/OptionsForFilter/OptionsForFilterSlice';

export const rootReducer = combineReducers({
  toggleOption: andOrToggleReducer,
  filteredByInput: inputFilterReducer,
  filteredByOptions: optionsFilterReducer,
});
